import React from 'react';
import {NavLink} from 'react-router-dom';

const SignedOutLinks = () =>{

 return (
     <ul>
        <li>
         <NavLink to="/signup">SignUp </NavLink>
        </li>
        <li>
          <NavLink to="/signin"> Login </NavLink>
        </li>
         
     </ul>
 );
}

export default SignedOutLinks;



